body {
    font-family: 'Source Sans Pro', sans-serif;
    font-weight: 400;
    line-height: 1.6;
    background-color: $color-primary;
    color: $color-white;


    min-height: 100vh;
}

.heading-primary {
    color: #fff;
    text-transform: uppercase;
 
    backface-visibility: hidden;
 
    margin-bottom: 3rem;
    
    
    
    &--main {
        display: block;
        font-size: 4rem;
        font-weight: 400;
        letter-spacing: 0.5rem;
        margin-bottom: 1rem;

        @include respond(tab-port){
            font-size: 3rem;
            letter-spacing: 0.2rem;
        }

        
    }

    &--sub {
        display: block;
        font-size: 1.8rem;
        font-weight: 700;
        letter-spacing: 0.2rem;
        line-height: 2.5rem;

        @include respond(tab-port) {
            font-size: 1.2rem;
            font-weight: 400;
        } 

     } 
}

.heading-secondary {
    font-size: 3.5rem;
    text-transform: uppercase;
    font-weight: 700;
    display: inline-block;
    background-image: linear-gradient(to right, $color-primary-light, $color-primary-dark);
    -webkit-background-clip: text;
    color: transparent;
    letter-spacing: .2rem;
    transition: all .2s;

    @include respond(tab-port) {
        font-size: 3rem;
    }

    @include respond(phone) {
        font-size: 2.5rem;
    }


    &:hover{
        transform: skewY(2deg) skewX(15deg) scale(1.1);
        text-shadow: .5rem 1rem 2rem rgba(#000, .2);
    }
}
.heading-tertiary {
    font-size: $default-font-size;
    text-transform: uppercase;
    font-weight: 700;
}
.paragraph {
    font-size: $default-font-size;
    &:not(:last-child){
        margin-bottom: 3rem;
    }
}
.link{
    
    &--bold{
        font-weight: 500;
    }
    &--inactive{
        pointer-events: none;
        color: rgba($color-primary, .5);
    }
}